  }
  .contact .card:hover{
    height: 350px;
    cursor: pointer;
  }
  .contact .card .img-box {
    position: absolute;
    top: 20px;
    left: 23px;
    width: 300px;
    height: 220px;
    background: #333;
    border-radius: 12px;
    overflow: hidden;
    transition: 0.5s;
}

.contact .card:hover .img-box {
